import React, { useState, useEffect } from "react";
import { Table } from "antd";
import { harcamaService } from "../services/harcama.services";

const TaksitList = (props) => {
  const [state, setState] = useState({ dataList: [] });

  

  useEffect(() => {
   harcamaService.taksitList().then((data) => {
   
   
    setState({ 
            dataList: data,
            selectedRowKeys: [],
         });
      });
  }, []);

  const columns = [
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
      
    },
    {
      title: 'Ödeyen',
      dataIndex: 'odeyen',
      key: 'odeyen',
    },
    {
      title: 'Miktar',
      dataIndex: 'miktar',
      key: 'miktar',
     
    },
    {
        title: 'Tarih',
        dataIndex: 'tarih',
        key: 'tarih',
      }
  ];

  const onSelectChange = selectedRowKeys => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    setState({ ...state,selectedRowKeys });
  };

  const rowSelection = {
    selectedRowKeys:state.selectedRowKeys,
    onChange:onSelectChange,
  };

  const summary= (pageData)=>{
    let sum=0;
    if(pageData.length>0){
      sum=pageData.map(item => item.miktar).reduce((prev, next) => prev + next);
    }

    return (
      <Table.Summary fixed>
        <Table.Summary.Row>
          <Table.Summary.Cell index={0}></Table.Summary.Cell>
          <Table.Summary.Cell index={1}></Table.Summary.Cell>
          <Table.Summary.Cell index={2}></Table.Summary.Cell>
          <Table.Summary.Cell index={3}>Miktar :{sum}</Table.Summary.Cell>
          <Table.Summary.Cell index={4}></Table.Summary.Cell>
        </Table.Summary.Row>
      </Table.Summary>
    )

  }


  return (
    <>
      <h1>Odenen Liste Ekranı</h1>
      <Table rowSelection={rowSelection} dataSource={state.dataList} columns={columns}  summary={summary} />;
      
    </>
  );
};

export default TaksitList;



